.App {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	height: 100vh;
	overflow: hidden;
}

.Weather-title {
	font-size: 48px;
	font-weight: 700;
	color: #bdbecb;
	text-align: center;
}

.Snow {
	--animation-name: snowfall;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 100;
	display: flex;
	justify-content: space-between;
	pointer-events: none;
}

.snow__flake {
	position: relative;
	top: -1.5em;
	color: #c1dcec;
	animation-name: var(--animation-name);
	animation-timing-function: ease-in-out;
	animation-iteration-count: infinite;
	will-change: transform;
}

@keyframes snowfall {
	0% {
		transform: translateY(0);
	}

	100% {
		transform: translateY(calc(100vh + 1.5em));
	}
}
